import React from 'react'

const light = {
    fontWeight: 'lighter'
}

function Tag({name, color}) {
    return (
        <div>
            <h4 style={light}> name: <span style={{fontWeight:'normal'}}>{name}</span>, color: <span style={{fontWeight:'normal'}}>{color}</span></h4>
        </div>
    )
}

export default Tag;